.App-main-section{
    background-color:#EBEBDF;
    padding: 40px;
    width: 80%;
}
.main-section-header{
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.header-layout-btn{

    width: 6rem;
    height: 3rem;
    background-color: #24282c;
    border-radius: 25px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}
.rounded-btn{
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    color: white;
    }
.rounded-btn.active{
    transition: all 0.1s ease-in-out;
    color: #24282c;
    background-color: white;
}
.header-search-container{
    width: 22rem;
    height: 3rem;
    border-radius: 30px;
    display: flex;
    align-items: center;
    justify-content:space-evenly;
    position: relative;
}

.search-input{
    font-size: 1rem;
    outline: none;
    height: inherit;
    width: 70%;
    border-radius: inherit;
    background-color: white;
    border: none;
    padding-left:3rem ;
}

.search-btn{
    background-color: white;
    width: 3rem;
    color: #24282c;
    border-radius: inherit;
    border: none;
    cursor: pointer;
    outline: none;
    height: inherit;
}

.search-icon{
    position: absolute;
    left: 30px;
    top:12px;
    cursor: pointer;
}